.mobCardDEE{width:70%;min-width:200px;height:auto;display:flex;flex-direction:column;justify-content:center;align-items:center;border-radius:0.8rem;font-family:poppins;margin:0 auto}.mobCardDEE .mobCardimg{margin-top:7.5%;width:80% img;width-width:100%}.mobCardDEE .titleDE{font-size:1.2rem;font-weight:bold;width:80%;display:flex;justify-content:center;align-items:center;text-align:center;padding:0.8rem}.mobCardDEE .titleDE .title_text{text-align:center}.mobCardDEE .cardBottom{background:#FFFFFF8F;width:100%;padding:1rem;display:flex;justify-content:space-between;align-items:center;border-radius:0 0 0.8rem 0.8rem;cursor:pointer}.mobCardDEE .cardBottom .viewDeets{font-size:0.875rem;font-weight:bold}@media all and (max-width: 400px){.mobCardDE{width:140px}}

.resCon{width:80%;margin:3rem auto;font-family:poppins}.resCon .heading{font-weight:bold;font-size:3.5rem}.resCon .resGrid{display:grid;margin-top:2rem;grid-column-gap:2rem;grid-row-gap:1rem;grid-template-columns:repeat(2, minmax(200px, 1fr))}@media all and (max-width: 800px){.resCon{margin-top:1.5rem}.resCon .heading{font-size:2.5rem;text-align:center}.resCon .resGrid{display:grid;margin-top:2rem;grid-column-gap:2rem;grid-row-gap:1rem;grid-template-columns:repeat(1, minmax(200px, 1fr))}}

.resCardCon{width:100%;font-family:"Poppins";display:flex;box-shadow:-4px 4px 14px rgba(0,0,0,0.25);background:white;border-radius:10px;padding:2rem}.resCardCon .info{width:50%;padding-right:0.5rem;display:flex;flex-direction:column;align-content:center;justify-content:center}.resCardCon .info .head{font-weight:bold;font-size:1.4rem}.resCardCon .info .infotext{font-size:0.8rem}.resCardCon .linkslist{width:50%;font-size:1rem;padding-left:0.5rem;display:flex;flex-direction:column;align-content:center;justify-content:center}.resCardCon .linkslist .fancylink{padding:0.4rem;font-weight:600;font-size:0.85rem;width:100%;margin-bottom:5px;background-color:rgba(163,157,157,0.151);border-radius:6px;cursor:pointer}.resCardCon .linkslist .fancylink:hover{transform:scale(1.03)}@media all and (max-width: 800px){.resCardCon{flex-direction:column}.resCardCon .info{width:100%}.resCardCon .linkslist{margin-top:0.7rem;width:100%}}

